import { PageContainer } from "@/components/layout/page-container";
import { Button } from "@/components/ui/button";
import { ShoppingBag, ArrowRight } from "lucide-react";
import Image from "next/image";
import Link from "next/link";

export default function Home() {
  return (
    <>
      {/* Hero Section */}
      <section className="relative h-[100vh] flex items-center bg-white/80 overflow-hidden">
        <PageContainer>
          <div className="grid gap-6 lg:grid-cols-[1fr_400px] lg:gap-12 xl:grid-cols-[1fr_600px]">
            <div className="flex flex-col justify-center space-y-4">
              <div className="space-y-2">
                <h1 className="text-3xl font-bold tracking-tighter sm:text-5xl xl:text-6xl/none">
                  Discover Authentic Khokana Flavors
                </h1>
                <p className="max-w-[600px] text-gray-500 md:text-xl dark:text-gray-400">
                  Experience the taste of tradition with our handcrafted
                  products from Khokana&apos;s Women Group.
                </p>
              </div>
              <div className="flex flex-col gap-2 min-[400px]:flex-row">
                <Button
                  asChild
                  size="lg"
                  className="bg-brand-red hover:bg-brand-red/90"
                >
                  <Link href="#products">
                    Shop Now
                    <ShoppingBag className="ml-2 h-5 w-5" />
                  </Link>
                </Button>
                <Button asChild size="lg" variant="outline">
                  <Link href="#about">
                    Learn More
                    <ArrowRight className="ml-2 h-5 w-5" />
                  </Link>
                </Button>
              </div>
            </div>
            <div className="flex items-center justify-center">
              <div className="relative aspect-square w-full max-w-[400px] animate-float">
                <Image
                  src="/placeholder.svg"
                  alt="Hero Product"
                  fill
                  className="object-contain"
                  priority
                />
              </div>
            </div>
          </div>
        </PageContainer>
        <HeroDecoration />
      </section>

      {/* Featured Products */}
      <section
        id="products"
        className="py-12 md:py-24 lg:py-32 bg-brand-yellow/30"
      >
        <PageContainer>
          <div className="flex flex-col items-center justify-center space-y-4 text-center">
            <div className="space-y-2">
              <h2 className="text-3xl font-bold tracking-tighter sm:text-5xl">
                Our Products
              </h2>
              <p className="max-w-[900px] text-gray-500 md:text-xl/relaxed lg:text-base/relaxed xl:text-xl/relaxed dark:text-gray-400">
                Discover our range of authentic local products, crafted with
                care by the women of Khokana.
              </p>
            </div>
          </div>
          {/* Product Grid - Replace with your actual products */}
          <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 mt-12">
            {Array.from({ length: 8 }).map((_, i) => (
              <div
                key={i}
                className="group relative overflow-hidden rounded-lg bg-white shadow-md transition-all hover:shadow-xl"
              >
                <div className="aspect-square overflow-hidden">
                  <Image
                    src="/placeholder.svg"
                    alt="Product"
                    width={400}
                    height={400}
                    className="object-cover transition-transform group-hover:scale-105"
                  />
                </div>
                <div className="p-4">
                  <h3 className="font-semibold">Product Name</h3>
                  <p className="text-sm text-gray-500">
                    Product description goes here
                  </p>
                  <div className="mt-4 flex items-center justify-between">
                    <span className="font-bold">$99.99</span>
                    <Button
                      size="sm"
                      className="bg-brand-red hover:bg-brand-red/90"
                    >
                      Add to Cart
                    </Button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </PageContainer>
      </section>

      {/* About Section */}
      <section id="about" className="py-12 md:py-24 lg:py-32 bg-white">
        <PageContainer>
          <div className="grid gap-6 lg:grid-cols-2 lg:gap-12">
            <div className="flex flex-col justify-center space-y-4">
              <div className="space-y-2">
                <h2 className="text-3xl font-bold tracking-tighter sm:text-4xl">
                  About Khona Paau
                </h2>
                <p className="text-gray-500 md:text-xl">
                  A product of Khokana's Women Group, empowering local
                  communities through traditional craftsmanship.
                </p>
              </div>
              <div className="space-y-4">
                <p>
                  Khona Paau is a community-driven initiative that celebrates
                  the rich culinary heritage of Khokana. Our products are
                  handcrafted by skilled women artisans using traditional
                  methods passed down through generations.
                </p>
                <p>
                  We take pride in preserving authentic flavors while supporting
                  sustainable livelihoods for local women. Each purchase
                  directly contributes to the economic empowerment of our
                  community members.
                </p>
              </div>
              <div>
                <Button asChild variant="outline" className="mt-4">
                  <Link href="#contact">
                    Contact Us
                    <ArrowRight className="ml-2 h-5 w-5" />
                  </Link>
                </Button>
              </div>
            </div>
            <div className="flex items-center justify-center">
              <div className="relative aspect-video w-full overflow-hidden rounded-xl">
                <Image
                  src="/placeholder.svg"
                  alt="About Khona Paau"
                  fill
                  className="object-cover"
                />
              </div>
            </div>
          </div>
        </PageContainer>
      </section>

      {/* Contact Section */}
      <section
        id="contact"
        className="py-12 md:py-24 lg:py-32 bg-brand-yellow/20"
      >
        <PageContainer>
          <div className="grid gap-6 lg:grid-cols-2 lg:gap-12">
            <div className="flex flex-col justify-center space-y-4">
              <div className="space-y-2">
                <h2 className="text-3xl font-bold tracking-tighter sm:text-4xl">
                  Get in Touch
                </h2>
                <p className="text-gray-500 md:text-xl">
                  Have questions or want to place an order? Contact us today.
                </p>
              </div>
              <div className="space-y-4">
                <div className="flex items-center space-x-3">
                  <div className="h-10 w-10 rounded-full bg-brand-red/10 flex items-center justify-center">
                    <span className="text-brand-red">üìç</span>
                  </div>
                  <div>
                    <h3 className="font-medium">Address</h3>
                    <p className="text-sm text-gray-500">
                      Khokana, Lalitpur, Nepal
                    </p>
                  </div>
                </div>
                <div className="flex items-center space-x-3">
                  <div className="h-10 w-10 rounded-full bg-brand-red/10 flex items-center justify-center">
                    <span className="text-brand-red">üìû</span>
                  </div>
                  <div>
                    <h3 className="font-medium">Phone</h3>
                    <p className="text-sm text-gray-500">+977 1234567890</p>
                  </div>
                </div>
                <div className="flex items-center space-x-3">
                  <div className="h-10 w-10 rounded-full bg-brand-red/10 flex items-center justify-center">
                    <span className="text-brand-red">‚úâÔ∏è</span>
                  </div>
                  <div>
                    <h3 className="font-medium">Email</h3>
                    <p className="text-sm text-gray-500">info@khonapaau.com</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="rounded-xl bg-white p-6 shadow-md">
              <form className="space-y-4">
                <div className="grid gap-4 sm:grid-cols-2">
                  <div className="space-y-2">
                    <label htmlFor="name" className="text-sm font-medium">
                      Name
                    </label>
                    <input
                      id="name"
                      className="w-full rounded-md border border-gray-300 p-2 text-sm focus:outline-none focus:ring-2 focus:ring-brand-red/20"
                      placeholder="Your name"
                    />
                  </div>
                  <div className="space-y-2">
                    <label htmlFor="email" className="text-sm font-medium">
                      Email
                    </label>
                    <input
                      id="email"
                      type="email"
                      className="w-full rounded-md border border-gray-300 p-2 text-sm focus:outline-none focus:ring-2 focus:ring-brand-red/20"
                      placeholder="Your email"
                    />
                  </div>
                </div>
                <div className="space-y-2">
                  <label htmlFor="subject" className="text-sm font-medium">
                    Subject
                  </label>
                  <input
                    id="subject"
                    className="w-full rounded-md border border-gray-300 p-2 text-sm focus:outline-none focus:ring-2 focus:ring-brand-red/20"
                    placeholder="Subject"
                  />
                </div>
                <div className="space-y-2">
                  <label htmlFor="message" className="text-sm font-medium">
                    Message
                  </label>
                  <textarea
                    id="message"
                    className="h-32 w-full rounded-md border border-gray-300 p-2 text-sm focus:outline-none focus:ring-2 focus:ring-brand-red/20"
                    placeholder="Your message"
                  />
                </div>
                <Button className="w-full bg-brand-red hover:bg-brand-red/90">
                  Send Message
                </Button>
              </form>
            </div>
          </div>
        </PageContainer>
      </section>
    </>
  );
}
